@mixin celular{
    @media only screen and (min-width: $telefono) {
        @content;
    }
}
@mixin tablet{
    @media only screen and (min-width: $tablet) {
        @content;
    }
}
@mixin desktop{
    @media only screen and (min-width: $desktop) {
        @content;
    }
}
@mixin grid($columnas, $espaciado){
    display: grid;
    grid-template-columns: repeat($columnas, 1fr);
    gap: $espaciado;
}